.goodNotification {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-color: #f6f6f6;
	padding: 45px 0;
	z-index: 1000;

	@include r($xs-breakpoint) {
		padding: rem(52px) 0 rem(45px);
	}

	> div {
		display: flex;
		align-items: center;
		justify-content: center;

		@include r($xs-breakpoint) {
			justify-content: space-between;
		}
	}

	&__title {
		text-transform: uppercase;
		font-size: 20px;
		letter-spacing: .2em;
		margin-right: 30px;

		@include r($xs-breakpoint) {
			font-size: rem(24px);
			line-height: rem(29px);
			margin-right: 0;
		}
	}

	.btn--primary {

		@include r($xs-breakpoint) {
			width: rem(127px);
			height: rem(44px);
			padding: 0;
			font-size: rem(24px);
		}
	}

	&__close {
		position: absolute;
		top: 50%;
		right: 82px;
		transform: translateY(-50%);

		@include r($xs-breakpoint) {
			display: none;
		}

		svg {
			width: 20px;
			height: 21px;
			stroke: $black;
		}
	}
}
